/* eslint-disable */

import './App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Navbar, Container, Nav, Row, Col} from 'react-bootstrap';
import { Suspense, lazy, useEffect, useState } from 'react';
import data from './data.js';
// import Detail from './routes/Detail.js';
// import Cart from './routes/Cart.js';
const Detail = lazy(() => import('./routes/Detail.js'));
const Cart = lazy(() => import('./routes/Cart.js'));
import { Routes, Route, useNavigate, Outlet } from 'react-router-dom';
import axios from 'axios';
import { useQuery } from '@tanstack/react-query';

function App() {

  let [shoes, upload] = useState(data);
  let navigate = useNavigate();
  let get_storage = JSON.parse(localStorage.getItem('watched'));

  useEffect(()=>{
    get_storage === null ? localStorage.setItem('watched', JSON.stringify([])) : null;
  },[]) 

  return (
    <div className="App">
      {/* 배너 */}
      <div className="Storage">
        <p className="View">Views</p>
        { get_storage !==null ? get_storage.map((a, i) => {
          return (
            <div>
                <img onClick={() => {navigate('/detail/'+(+get_storage[i]))}} className="Storage_img" src={'https://codingapple1.github.io/shop/shoes'+(get_storage[i]+1)+'.jpg'}></img>
              </div>
          ); 
        })
      :null }
      </div>

      {/* 네비바 */}
      <Navbar bg="light" variant="light">
        <Container>
          <div className="logo">
          <i class="fa-solid fa-shoe-prints" style={{paddingRight: '8px', fontSize:'20px'}}></i>
          <Navbar.Brand onClick={ () => {navigate('/')}}>ABC MART</Navbar.Brand></div>
          <Nav className="me-auto">
            <Nav.Link onClick={ () => {navigate('/')}}>Home</Nav.Link>
            <Nav.Link onClick={ () => {navigate('/cart')}}>Cart</Nav.Link>
          </Nav>
          <Nav className="sns" style={{fontSize:'20px'}}>
            <a><i style={{paddingRight:'8px'}} class="fa-brands fa-instagram"></i></a>
            <a><i class="fa-brands fa-facebook"></i></a>
          </Nav>
        </Container>
      </Navbar>

      <Suspense fallback={<div>로딩중</div>}>
      <Routes>
        {/* 메인페이지 */}
        <Route path="/" element={
          <>
          <div className="main-photo"/>
          <Container>
            <Row>
            { shoes.map(function(a, i) {
                return <Items navigate={navigate} shoes={shoes[i]} i={i+1}/>
            })}
            </Row>
          </Container>
          <button onClick={() => {
            axios.get('https://codingapple1.github.io/shop/data2.json')
            .then((result) => {
              let copy = [...shoes];
              copy = copy.concat(result.data);
              // copy = [...shoes, ...result.data];
              upload(copy);
             
            })
            .catch(() => {
            })
          }}>More</button>
          </>  
        }/>
        {/* 상세페이지 */}
        <Route path="/detail/:id" element={
        <Detail shoes={shoes} />
        } />
        <Route path="/cart" element={
        <Cart/>} />
      </Routes>
      </Suspense>
      
    </div> 
  );
}

function Event() {
  return (
    <div>
      <h4>오늘의 이벤트</h4>
      {/* nested route을 보여줄 장소 */}
      <Outlet></Outlet>
    </div>
  )
}

function Items(props) {
  let navigate = useNavigate();
  return (
    <Col sm={4} onClick={ () => {navigate('/detail/' + (props.i-1)); }}>
      <img src={'https://codingapple1.github.io/shop/shoes' +props.i+ '.jpg'} width='80%'/>
      <h4>{props.shoes.title}</h4>
      <p>{props.shoes.price}</p>
    </Col>
  )
}

export default App;